import React from 'react';
import { asField } from 'informed';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';

export default withStyles(null)(asField(({ fieldState, fieldApi, ...props }) => {
    const {
        value,
        error
    } = fieldState;
    const {
        setValue,
        setTouched
    } = fieldApi;
    const {
        onChange,
        onBlur,
        forwardedRef,
        field: name,
        options,
        selected = null,
        ...rest
    } = props;
    console.log(selected);
    return (
        <FormControl fullWidth className="mt3">
            <InputLabel htmlFor={name} className="ttc">{name}</InputLabel>
            <Select
                {...rest}
                ref={forwardedRef}
                value={value || selected}
                onChange={e => {
                    setValue(e.target.value)
                    if (onChange) {
                        onChange(e)
                    }
                }}
                onBlur={e => {
                    setTouched()
                    if (onBlur) {
                        onBlur(e)
                    }
                }}
                input={<Input name={name} />}
            >
                {/* <MenuItem value="" selected>
                    <em>None</em>
                </MenuItem> */}
                {options.map(e => <MenuItem value={e.id} key={e.id}>{e.username}</MenuItem>)}
            </Select>
            {
                error ? <FormHelperText error={true}>{error}</FormHelperText> : null
            }
        </FormControl>
    );
}));



